// Header text feature
.call-out {
  padding: 6em $padding-large;
  @media (max-width: 1000px) {
    padding: 2em 2em;
  }
  @media (max-width: 576px) {
    padding: 1em 1em;
  }

  display: inline-block;
  width: 100%;
  height: 100%;
  font-size: 1.2em;
  text-align: center;
  color: $header-desc-text-color;
  background: $header-desc-background-color no-repeat;

  background-position: center center !important;
  -webkit-background-size: cover; /* pour Chrome et Safari */
  -moz-background-size: cover; /* pour Firefox */
  -o-background-size: cover; /* pour Opera */
  background-size: cover; /* version standardisée */
  @media only screen and (min-width : 320px)  {} // background isn't fixed here.
  @media only screen and (min-width : 480px)  {} // background still isn't fixed here.
  @media only screen and (min-width : 768px)  {} // background still isn't fixed here.
  @media only screen and (min-width : 992px)  {background-attachment: fixed;} // Ok NOW the background is fixed.
  @media only screen and (min-width : 1200px) {} // background is STILL fixed here.

  //For white images
  p,
  a,
  li,
  h1,
  h2,
  h3,
  h4,
  h5 {
    @extend .header-txt-shadow;
  }

  p:last-child {
    margin-bottom: 0;
  }
}

.welcome-banner {
  padding-top: 6em;
  padding-bottom: 6em;

  @media screen and (max-width: $break) {
    padding-top: 3em;
    padding-bottom: 3em;
  }
}

h1.news-title {
  padding: 1em 0 0 0;
  font-size: 3em;
  text-align:center;
  display: flex;
  flex-direction: row;
  justify-content: center;
}
h1.news-title:before, h1.news-title:after {
  background-color: $search-color;
  content: '\a0';
  flex-grow: 1;
  height: 1px;
  position: relative;
  top: 0.6em;
}
h1.news-title:before {
  margin-right:15px;
}
h1.news-title:after {
  margin-left:15px;
}


#main {
  background-position: 100% 0, 0 center, center top;
}

// Post listing
.posts-home{

}

.posts {
  .post-teaser {
    @extend %padding-regular;

    width: 100%;
    margin-bottom: 0;
    display: inline-block;
    background-size: $feature-image-size;
    border-bottom: 1px solid $border-color;

    @media screen and (max-width: $break) {
      h1 {
        font-size: 1.8em !important;
      }
    }

    p a {
      @extend .body-link;
    }
    img {
      width: 100%;
      padding: 0;
      vertical-align: middle;
    }

    .post-img {
      vertical-align: middle;
      border-radius: 10px;
      overflow: hidden;

      @media (min-width: $break) {
        height: 300px;
      }
      //Smaller screen
      @media screen and (max-width: $break) {
        height: 200px;
      }
    }

    .post-img img {
      //max-width: 100%;

      -moz-transition-timing-function: ease-out;
      -webkit-transition-timing-function: ease-out;
      transition-timing-function: ease-out;

      -moz-transition: all 2s;
      -webkit-transition: all 2s;
      transition: all 2s;

      &:hover {
        -moz-transform: scale(1.1);
        -webkit-transform: scale(1.1);
        transform: scale(1.1);
      }
    }
  }
  .excerpt {
    margin-top: 1em;
  }
}

// Pagination
.pagination-articles {
  padding: $padding-small $padding-large 0 $padding-large;
  text-align: center;
  @media (max-width: 600px) {
    padding: $padding-small;
  }

  .button {
    margin: 0 1.5em;
    i {
      vertical-align: middle;
    }
  }

}

// Animated image
.animated-zoom-img {
  vertical-align: middle;
  overflow: hidden;

  @media (min-width: $break) {
    height: 250px;
  }
  //Smaller screen
  @media screen and (max-width: $break) {
    height: 150px;
  }
}

.animated-zoom-img img {
  -moz-transition-timing-function: ease-out;
  -webkit-transition-timing-function: ease-out;
  transition-timing-function: ease-out;

  -moz-transition: all 2s;
  -webkit-transition: all 2s;
  transition: all 2s;

  &:hover {
    -moz-transform: scale(1.1);
    -webkit-transform: scale(1.1);
    transform: scale(1.1);
  }
}
