.site-header {
  //padding-bottom: 1%;
  padding: 2% 2% 1% 2%;
  background: $brand-color;
  display: inline-block;
  float: left;
  width: 100%;

  .separator {
    user-select: none;
    opacity: $navbar-separator-opacity;

    &:first-child {
      display: none;
    }
  }

  a {
    color: $header-link-color;
  }

  .avatar {
    height: 3em;
    width: 3em;
    float: left;
    margin-top: -13px;
    border-radius: 0.2em;
    margin-right: 0.5em;
    padding: 10px 10px;

  }

  .site-title {
    float: left;
    font-size: 1em;
    line-height: 1.5;
  }

  nav {
    list-style: none;
    margin: 0;
    padding: 0;
    ul {
      margin: 0;
      padding: 0;
      list-style: none;
      line-height: 1.5;
      float: right;
      text-align: right;
      display: block;
    }
    li {
      display: inline;
      float: left;
      margin-right: 0.8em;
    }
    a {
      &:hover {
        color: $link-color;
      }
      &#pull {
        i {
          margin-top: 13px;
          float: right;
        }

      }

    }
  }
}

// To clear things when we float the element inisde nav and ul
.clear {
  *zoom: 1;

  &:after {
    clear: both;
  }

  &:before,
  &:after {
    content: " ";
    display: table;
  }
}

/* ---- Responsive ---- */

@media (min-width: $break) {
  a#pull {
    display: none;
  }
}

@media screen and (max-width: $break) {
  .site-header {
    img.avatar {
      margin-top: -7px
    }
    .site-title {
      margin-top: 7px;
    }
  }

  nav {
    height: auto;

    .separator {
      display: none !important;
    }

    ul {
      width: 100%;
      overflow: hidden;
      height: auto;

      /* fade in */
      transition: font-size .25s,
      margin .25s,
      padding .25s,
      opacity .5s .25s,
      height .5s;
    }

    ul.hide {
      font-size: 0;
      margin: 0;
      opacity: 0;
      padding: 0;
      height: 0;

      /* fade out, then shrink */
      transition: opacity .25s,
      font-size .5s .25s,
      margin .5s .25s,
      padding .5s .25s,
      height .5s;

    }

    ul.toggleMenu {
      background-color: #e9ecef;
      width: 110%;
      margin: -1% -2%;
      padding: 2% 0%;

      li {
        display: block;
        float: none;
        list-style: none;
        padding: 1% 0%;
        font-size: 1.2em;
        text-align: center;
      }
    }

    /*
    li {
      width: 100%;
      float: left;
      position: relative;
    }
    */

    a {
      text-align: left;
      width: 100%;

      &#pull {
        display: block;
        width: 100%;
        position: relative;

        &:after {
          content: "";
          display: inline-block;
          position: absolute;
          right: 15px;
          top: 10px;
        }
      }
    }
  }
}